.input {
  margin: 2rem auto;
  padding: 1rem;
  width: 90%;
  max-width: 40rem;
  background-color: #ee4747;
}

.input label {
  display: block;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.input input {
  font: inherit;
  display: block;
  width: 100%;
  border: 1px solid #d1d1d1;
  padding: 0.15rem;
  background-color: #ffcece;
  margin-bottom: 0.5rem;
}

.input input:focus {
  outline: none;
  border-color: #5f0000;
}
