.button {
  font: inherit;
  border: 1px solid #7f0404;
  border-radius: 8px;
  background: #670205;
  color: white;
  padding: 0.25rem 1rem;
  cursor: pointer;
}

.button:hover,
.button:active {
  background: #b60a0a;
  border-color: #c00f0f;
}

.button:focus {
  outline: none;
}
